.flex, html, body, .header-container, .button-container, .container, .card, .profile-stats, .profile-stats--stat {
  display: flex;
}

.flex-rem, .header-container, .container, .profile-stats--stat {
  flex-grow: 1;
}

.layout-column, body, .container, .profile-stats--stat {
  flex-direction: column;
  height: 100%;
}

.layout-row, .header-container, .card, .profile-stats {
  flex-direction: row;
}

.layout-center {
  align-items: center;
}

.layout-fill {
  min-height: 100%;
  min-width: 100%;
  height: 100%;
  width: 100%;
}

.layout-wrap {
  flex-wrap: wrap;
}

.layout-align-start-center, .header-container, .button-container {
  align-items: center;
}

.layout-align-end-center {
  align-items: center;
  justify-content: flex-end;
}

.layout-align-center-center, .profile-stats--stat {
  align-items: center;
  justify-content: center;
}

.layout-align-space-between-center {
  align-items: center;
  align-content: space-between;
}

.block--center, .container {
  margin: 0 auto;
}

.pad-0 {
  padding: 0;
}

.pad-l-10, .input--search-container .input--search {
  padding-left: 0.625em;
}

.pad-lr-10 {
  padding-left: 0.625em;
  padding-right: 0.625em;
}

.pad-r-10 {
  padding-right: 0.625em;
}

.pad-box-10, .profile-stats {
  padding: 0.625em;
}

.pad-box-20, .card {
  padding: 20px;
}

.pad-td-20 {
  padding-top: 20px;
  padding-bottom: 20px;
}

.margin-0 {
  margin: 0;
}

.margin-l-10 {
  margin-left: 0.625em;
}

.margin-l-20 {
  margin-left: 20px;
}

.margin-lr-10 {
  margin-left: 0.625em;
  margin-right: 0.625em;
}

.margin-lr-20 {
  margin-left: 20px;
  margin-right: 20px;
}

.margin-r-10 {
  margin-right: 0.625em;
}

.margin-r-20, .tweet-menu div {
  margin-right: 20px;
}

.margin-d-10 {
  margin-bottom: 0.625em;
}

.margin-t-20 {
  margin-top: 20px;
}

.margin-td-10 {
  margin-top: 0.625em;
  margin-bottom: 0.625em;
}

.margin-td-20, .profile-stats {
  margin-top: 20px;
  margin-bottom: 20px;
}

.background-color-white, .header-container, .card {
  background-color: #fff;
}

.background-color-grey, body {
  background-color: #f5f8fa;
}

.border-right, .right-separator {
  border: none;
  border-right: solid thin rgba(0, 0, 0, 0.1);
}

.border-left {
  border: none;
  border-left: solid thin rgba(0, 0, 0, 0.1);
}

.border-top {
  border: none;
  border-top: solid thin rgba(0, 0, 0, 0.1);
}

.border-bottom, .menu--active, .stat--active {
  border: none;
  border-bottom: solid thin rgba(0, 0, 0, 0.1);
}

.border-box, .button--rounded, .input--search-container .input--search {
  border: solid thin rgba(0, 0, 0, 0.1);
}

html, body {
  margin: 0;
  height: 100%;
  font-family: 'Open Sans', sans-serif;
}

body {
  font-family: "Lato", "Helvetica Neue","Open Sans", Arial, sans-serif;
}

p {
  color: rgba(0, 0, 0, 0.65);
}

/******* HEADER *****/
.header-container {
  height: 70px;
  color: rgba(0, 0, 0, 0.65);
}

.main-menu--container {
  height: inherit;
}

.menu--mobile {
  display: none;
  padding: 0;
}

.menu--mobile div {
  padding: 0.625em;
  color: rgba(0, 0, 0, 0.65);
}

.menu--active {
  border-bottom-color: #55acee;
}

.button-container {
  height: 100%;
  padding-left: 0.9375em;
  padding-right: 0.9375em;
}

.button, .profile-stats--stat {
  cursor: pointer;
}

.button--rounded {
  height: 35px;
  width: 35px;
  min-width: 35px;
  border-radius: 50%;
  text-align: center;
  line-height: 35px;
  font-size: 0.875em;
}

.button--active {
  background-color: #55acee;
  color: #fff;
  border-color: #55acee;
}

.logo {
  background: url("assets/logo.png");
  width: 28px;
  height: 28px;
  margin-left: 0.625em;
  margin-right: 0.625em;
  background-size: contain;
  background-repeat: no-repeat;
}

.input--search-container {
  position: relative;
}

.input--search-container:after {
  position: absolute;
  content: '\f002';
  height: 0.875em;
  width: 0.875em;
  display: inline-block;
  font: normal normal normal 0.875em/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 35px;
  right: 0.9375em;
  bottom: 23px;
  color: rgba(0, 0, 0, 0.35);
}

.input--search-container .input--search {
  padding-right: 1.875em;
  border-radius: 20px;
  height: 33px;
  width: 150px;
  outline: none;
  color: rgba(0, 0, 0, 0.65);
  font-size: 0.875em;
}

/*************Header End *********/
.container {
  width: 50%;
}

.card {
  box-shadow: 0 0 0 1px rgba(0, 1, 0, 0.05);
}

.tweet-card h4 {
  color: rgba(0, 0, 0, 0.85);
}

.profile--img {
  background: url("assets/profile-img.png");
  background-position: center;
}

.profile-stats {
  padding: 0;
}

.profile-stats p {
  margin: 0;
}

.profile-stats span {
  font-weight: bolder;
}

.profile-stats--container {
  min-height: 72px;
}

.profile-stats--stat {
  padding: 0.9375em;
  font-size: 90%;
  height: inherit;
}

.stat--active {
  border-width: 4px;
  border-color: #55acee;
}

.tweet-menu span, .tweet-menu i {
  color: rgba(0, 0, 0, 0.35);
}

.tweet-content {
  margin: 8px 0;
}

.tweet-content p {
  margin: 0;
}

.tweet-user, .tweet-moment {
  color: rgba(0, 0, 0, 0.35);
  font-weight: 400;
  padding-left: 5px;
}

/**** Responsive ***/
/* Smartphones (portrait and landscape) ----------- */
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  /* Styles */
  .container {
    width: 98%;
  }
  .main-menu--container {
    display: none;
  }
  .main-search {
    flex-grow: 1;
    max-width: 50%;
  }
  .menu--mobile {
    display: flex;
  }
}

@media (min-width: 600px) and (max-width: 799px) {
  .container {
    width: 85%;
  }
}

@media (min-width: 801px) and (max-width: 992px) {
  .container {
    width: 85%;
  }
}

/* Smartphones (portrait) ----------- */
@media only screen and (max-width: 320px) {
  /* Styles */
  .container {
    width: 98%;
  }
}
